<template>
        <div>
            <headers class="mui-bar mui-bar-nav">
                <h1 class="mui-title">资讯</h1>
            </headers>
            <sclips>
            </sclips>
            <news :fn="fn">
                <li class="media" v-for="item in datas">
                    <router-link :to="'/newshow/'+item.nid">
                        <div class="media-left media-middle" >
                            <a>
                                <img class="media-object" :src="item.thumb" alt="加载失败">
                            </a>
                        </div>
                        <div class="media-body">
                            <h4 class="media-heading">{{item.title}}</h4>
                        </div>
                    </router-link>
                </li>
            </news>
            <footers>
            </footers>
        </div>
</template>
<script>
    import headers from '@/components/header'
    import news from '@/components/news'
    import sclips from "@/components/sclip";
    import footers from '@/components/footer'

export default {
    data(){
        return {
            datas:[],
            num:0,
            deg:20,
            degs:0,
        }
    },
    created(){
        if(this.$store.state.num){
            this.num=this.$store.state.num;
        }


        if(this.$store.state.datae.length==0){
            fetch('/home/nesShow?degs='+this.$store.state.deg+'&deg='+this.deg).then(function (e) {
                return e.json();
            }).then((e)=>{
                this.datas=e;

            })
        }else {
            this.datas=this.$store.state.datae
        }

    },
    methods:{
      fn(rot){
          rot.show=true;
          this.num++;
              this.degs = this.deg * this.num;
          fetch('/home/nesShow?degs='+this.degs+'&deg='+this.deg).then(function (e) {
              return e.json();
          }).then((e)=>{
              if(e.length>0){
                  rot.show=false;
                  this.datas=e.concat(this.datas);
                  rot.message='刷新成功';
                  this.$store.commit('setdatae',this.datas)
                  this.$store.commit('setnum',this.num)
                  this.$store.commit('setdeg',this.degs)
              }else {
                  rot.show=false;
                  rot.message='刷新失败';
              }

          })
      }
    },
    components:{sclips,news,headers,footers},




}
</script>
<style scoped>
    .media-object{
        width: 64px;
        height: 64px;
        line-height: 64px;
        font-size: 12px;
        text-align: center;
    }
    .media-heading{
        font-size: 16px;
    }



</style>


