<template>
    <div class="downBox" ref="down" @touchstart="start($event)" >
        <div class="downup" v-show="show">
                {{message}}
        </div>
        <ul class="index-content mui-table-view" ref="junp">
            <slot></slot>
        </ul>
    </div>

</template>
<script>
export default {
    data(){
        return {
            message:'下拉刷新',
            startY:'',
            moveY:'',
            download:null,
            show:false,
            list:'',
            dome:null,
            junp:'',
            flag:false
        }
    },
    props:['fn'],
    mounted(){
        this.dome=this.$refs.down;
        this.download=this.dome.getElementsByClassName('index-content')[0];
        var that=this;
        window.onscroll=function(){
            that.junp=(document.documentElement.scrollTop)
            if(this.junp=='0'){
                that.flag=true;
            }else {
               that.flag=false;
            }

        }
    },
    methods:{
        start(e){
            console.log(this.junp);
            if(this.junp==0){

                this.download.style.transition="none"
                this.startY=e.touches[0].pageY;
                this.dome.addEventListener('touchmove',this.move)
                this.dome.addEventListener('touchend',this.end)
            }
        },
        move(e){
            this.show=true;
            this.message='下拉刷新';
            this.moveY=e.touches[0].pageY;
            this.download.style.transform=this.moveY-this.startY>0?`translateY(${this.moveY-this.startY}px)`:'translateY(0px)';

        },
            end(e){
            this.dome.removeEventListener("touchmove",this.move);
            this.dome.removeEventListener("touchend",this.end);
                if(this.moveY){
                    if(this.moveY-this.startY>20){
                        this.download.style.transform=`translateY(0px)`;
                        this.fn(this);
                    }
                }

                this.download.style.transition="transform 1s ease"
        }
    }
}
</script>
<style scoped>
    .downBox{
        margin-top: 5px;
        margin-bottom: 48px;
    }
.index-content{
    width: 100%;
    padding: 12px;

}
    .index-content>li{
        width: 100%;
        border-bottom:1px solid #ccc;

    }
    .index-content>li>a{
        font-size: 14px;
        color:black;
    }
    .downup{
        width: 100%;
        height:20px;
        text-align: center;
        line-height: 20px;
        font-size: 12px;
        color: black;
    }
</style>


