<template>
    <div class="sclip" >
        <ul class="sclip-list" style="height: 100%" @touchstart="start($event)" ref="content">
            <li class="sclip-list-content" v-for="item in banner" ref="lis">
                <img :src="'http://localhost:7777/'+item.img" alt="加载失败">
            </li>
        </ul>
    </div>

</template>
<script>

    export default {
        data(){
            return {
                nums:3,
                startX:'',
                startY:'',
                moveY:'',
                moveX:'',
                sclip:null,
                currebt:'',
                length:50,
                direction:'',
                movenum:0,
                banner:[]
            }
        },
        created(){
            fetch('/home/banner').then(function (e) {
                return e.json();
            }).then((e)=>{
                this.banner=e;
                this.nums=e.length;
            });
        },
        updated(){
            var sclip=this.$refs.content;
            var lsit=sclip.getElementsByClassName('sclip-list-content');
            sclip.style.width=100*this.nums+'%';
            for(var i=0;i<this.nums;i++){
                lsit[i].style.width=100/this.nums+'%';

            }
            this.sclip=sclip;
        },
        methods:{
            start(e){
                this.sclip.style.transition="none"
                this.startX=(e.touches[0].pageX);
                this.startY=(e.touches[0].pageY);
                this.sclip.addEventListener("touchmove",this.move)
                this.sclip.addEventListener("touchend",this.end)
                this.currebt=parseInt(getComputedStyle(this.sclip,null)["marginLeft"])?parseInt(getComputedStyle(this.sclip,null)["marginLeft"]):0;


            },
            move(e){
                this.moveX=e.touches[0].pageX;
                this.moveY=e.touches[0].pageY;
                if(Math.abs(this.moveX-this.startX)>3){
                    if(this.moveX>this.startX){
                        this.direction='right'
                        this.sclip.style.marginLeft=this.currebt+this.moveX-this.startX+'px';
                    }else{
                        this.direction='left'
                        this.sclip.style.marginLeft=this.currebt+this.moveX-this.startX+'px';
                    }
                }

            },
            end(e){
                this.currebt=document.body.clientWidth;
                this.sclip.removeEventListener("touchmove",this.move)
                this.sclip.removeEventListener("touchend",this.end)
                if(this.direction=='right'){
                    if(Math.abs(this.moveX-this.startX)>this.length){
                        this.movenum++;
                        if(this.movenum>0){
                            this.movenum=0
                        }
                    }
                }else if(this.direction=='left'){
                    if(Math.abs(this.moveX-this.startX)>this.length){
                        this.movenum--;
                        if(this.movenum<-(this.nums-1)){
                            this.movenum=-(this.nums-1)
                        }
                    }
                }
                this.sclip.style.transition="margin 1s ease"
                this.sclip.style.marginLeft=this.currebt*this.movenum+'px';
            }
            }

    }
</script>
<style>
    .sclip-list-content>img{
        width: 100%;
        height: 100%;
    }
    .sclip-list-content{
        height: 100%;
        float: left;
    }
    .sclip{
        width: 100%;
        height: 200px;
        overflow: hidden;
        margin-top: 44px;
    }
</style>


